/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  appearance: none;
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  border: none;
  color: inherit;
  font: inherit;
  cursor: pointer;
  padding: 0;
  margin: 0;
}

@font-face {
  font-family: "GmarketSansBold";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansBold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "GmarketSansMedium";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "GmarketSansLight";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansLight.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "NEXON Lv1 Gothic OTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv1 Gothic OTF Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
.type-heading {
  position: absolute;
  left: -20px;
  top: 0;
  transform: translateX(-100%);
  font-size: 14px;
  font-weight: 700;
  color: var(--color-black);
  opacity: 0.4;
}
@media all and (max-width: 767px) {
  .type-heading {
    font-size: 13px;
    left: 0;
    top: -10px;
    transform: translateY(-100%);
  }
}
.type-main {
  font-size: 16px;
  font-weight: 700;
}
.type-normal {
  font-size: 13px;
  font-weight: 400;
}
.type-big {
  font-size: 24px;
  font-weight: 700;
}
.type-black {
  color: var(--color-black);
}
.type-white {
  color: var(--color-black);
}

:root {
  --color-black: #1f1f1f;
  --color-white: #fcfcfc;
  --color-gray: #4f4f4f;
}

.page-wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  z-index: 1;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.page-wrap::-webkit-scrollbar {
  display: none;
}
.page-inner {
  display: block;
  box-sizing: border-box;
  padding: 0 100px 100px 200px;
}
@media all and (max-width: 767px) {
  .page-inner {
    padding-bottom: 70px;
    padding-left: 100px;
    padding-right: 20px;
  }
}
.page-section {
  margin-bottom: 100px;
}
@media all and (max-width: 767px) {
  .page-section {
    margin-bottom: 60px;
  }
}

.section-wrap {
  position: relative;
  max-width: 1024px;
  margin: 0 auto;
}
@media all and (max-width: 767px) {
  .section-wrap {
    margin: 0 8vw;
  }
}
.section-inner.inner-padding {
  padding: 30px 45px;
}
.section-title {
  font-weight: 700;
  margin-bottom: 20px;
}

.sticky-wrap {
  position: relative;
  width: 100%;
  height: calc(2 * var(--vh));
}
.sticky-inner {
  position: sticky;
  top: 0;
  height: var(--vh);
}

.loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
}
.loading.off {
  display: none;
}
.loading-inner {
  position: relative;
  width: 100%;
  height: 100%;
}
@media (prefers-color-scheme: dark) {
  .loading-inner {
    background-color: var(--color-bg);
  }
}

html, body {
  padding: 0;
  margin: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

body {
  font-family: "NEXON Lv1 Gothic OTF";
  font-size: 15px;
  font-weight: 400;
  line-height: 1.2;
  color: var(--color-white);
}
@media all and (max-width: 767px) {
  body {
    font-size: 13px;
    line-height: 1.3;
  }
}

main {
  background-color: var(--color-black);
}

#p5 {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 110vw;
  height: 110vh;
  z-index: 2;
  overflow: hidden;
  pointer-events: none;
}
@media all and (max-width: 767px) {
  #p5 {
    width: 105vw;
    height: 105vh;
  }
}
#p5 canvas {
  position: relative;
  width: 100%;
  height: 100%;
}

.package {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: var(--vh);
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  z-index: 10;
}
.package-title {
  display: flex;
  width: 100px;
  height: 100px;
  align-items: center;
  justify-content: center;
  z-index: 1;
  background-color: var(--color-white);
}
@media all and (max-width: 767px) {
  .package-title {
    width: 80px;
    height: 80px;
  }
}
.package-title span {
  font-size: 13px;
  color: var(--color-black);
}
@media all and (max-width: 767px) {
  .package-title span {
    font-size: 12px;
  }
}
.package-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-white);
}
.package-bg.hide {
  opacity: 0;
}
.package-guide {
  position: absolute;
  top: 80%;
  font-size: 14px;
  color: var(--color-black);
  opacity: 0.5;
}
@media all and (max-width: 767px) {
  .package-guide {
    font-size: 12px;
  }
}
.package-guide span {
  animation: blink 2.4s infinite;
}

.block {
  --margin: 15px;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin-bottom: var(--margin);
  margin-right: var(--margin);
  cursor: default;
}
@media all and (max-width: 767px) {
  .block {
    --margin: 10px;
  }
}
.block-list {
  font-size: 0;
  margin-right: calc(-1 * var(--margin));
}
.block-inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  word-break: keep-all;
  box-sizing: border-box;
  width: 150px;
  height: 150px;
  padding: 10px;
  border: 1px solid var(--color-white);
}
@media all and (max-width: 767px) {
  .block-inner {
    width: 120px;
    height: 120px;
    padding: 5px;
  }
}
.block-inner.back {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s ease-out, visibility 0.4s ease-out;
  background-color: var(--color-white);
}
.block.active .block-inner.back {
  opacity: 1;
  visibility: visible;
}
.block .title {
  font-size: 15px;
  color: var(--color-white);
}
@media all and (max-width: 767px) {
  .block .title {
    font-size: 14px;
  }
}
.block .sub {
  font-size: 12px;
  margin-top: 8px;
}
.block .sub.round {
  padding: 3px 8px;
  border-radius: 50px;
  background-color: var(--color-white);
  font-weight: 700;
  color: var(--color-black);
}
.block .desc {
  font-size: 13px;
  color: var(--color-black);
}
@media all and (max-width: 767px) {
  .block .desc {
    font-size: 12px;
  }
}
.block .tag {
  font-size: 10px;
  color: var(--color-black);
  margin-right: 5px;
  margin-bottom: 5px;
}

.github {
  width: 30px;
}
@media all and (max-width: 767px) {
  .github {
    width: 24px;
  }
}
.github .color {
  fill: var(--color-white);
  transition: fill 0.4s ease-out;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}